import React from "react";
import GetFilmInfo from "./GetFilmInfo";
import "./components.css";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
export default class Watchlist extends React.Component {
  state = {
    posters: [],
  };

  componentDidMount() {
    // if persistent storage holds data for watchlist, the watchlist is set
    // otherwise only text "empty list" will be shown
    try {
      let titles = JSON.parse(localStorage.getItem("titles"));
      let images = JSON.parse(localStorage.getItem("images"));
      console.log(titles);
      console.log(images);
      this.setState({ names: titles });
      this.setter(titles, images);
    } catch (error) {
      console.log(error);
      this.setState({ posters: <p> List empty </p> });
    }
  }

  // function to clear the whole watchlist
  ClearList = () => {
    localStorage.clear();
    this.setState({ posters: <p> </p> });
  };

  // function to change the url in the App-component
  change = (name) => {
    let key2 = "3e5977f9";
    let url = `https://www.omdbapi.com/?t=${name}&apikey=${key2}`;
    this.props.set(url);
  };

  // function to remove individual films from the list
  remove = (film, image) => {
    let titles = JSON.parse(localStorage.getItem("titles"));
    let images = JSON.parse(localStorage.getItem("images"));
    let newTitles = [];
    let newImages = [];
    for (let i = 0; i < titles.length; i++) {
      if (titles[i] !== film) {
        newTitles.push(titles[i]);
        newImages.push(images[i]);
      }
    }
    localStorage.setItem("titles", JSON.stringify(newTitles));
    localStorage.setItem("images", JSON.stringify(newImages));
    console.log(newTitles);
    this.setter(newTitles, newImages);
  };

  // setting up the component according to the title and image it receives as parameter
  // images will once again serve as links to film's page
  setter = (ttl, img) => {
    let key2 = "3e5977f9";
    const images = [];

    for (let i = 0; i < img.length; i++) {
      images[i] = (
        <>
          <td>
            {" "}
            <Link
              to={"/GetFilmInfo"}
              className="nav-link"
              onClick={() => this.change(ttl[i])}
            >
              <img
                key={i}
                src={img[i]}
                alt="poster"
                width="200"
                height="280"
                onClick={() => {
                  <GetFilmInfo
                    URL={`https://www.omdbapi.com/?t=${ttl[i]}&apikey=${key2}`}
                  />;
                }}
              />
              <Route path="/GetFilmInfo">
                <GetFilmInfo
                  URL={`https://www.omdbapi.com/?t=${ttl[i]}&apikey=${key2}`}
                />
              </Route>
            </Link>
            <p className="list">
              {ttl[i]}
              <p>
                <button
                  className="b"
                  onClick={() => this.remove(ttl[i], img[i])}
                >
                  Delete
                </button>
              </p>
            </p>
          </td>
        </>
      );
    }
    this.setState({ posters: [<p>{images}</p>] });
  };

  render() {
    return (
      <>
        <p>
          <button className="b" onClick={() => this.ClearList()}>
            Clear List
          </button>{" "}
        </p>
        <table> {this.state.posters}</table>
      </>
    );
  }
}
